import { useNavigate } from "react-router-dom";
import "./App.css";
import "./video-react.css"; // import css
import {
  Player,
  ControlBar,
  ReplayControl,
  ForwardControl,
  CurrentTimeDisplay,
  TimeDivider,
  PlaybackRateMenuButton,
  VolumeMenuButton
} from 'video-react';

function App() {
  const navigate = useNavigate();
  const handleClick = () => {
    navigate("/work");
  };
  return (
    <div className="App">
      <img onClick={handleClick} className="logo" src="logo.png" />
      <Player
        className="video"
        autoPlay
        muted
        loop
        playsInline
        poster="/assets/poster.png"
        src="final.webm"
      >
       <ControlBar autoHide autoHideTime={0}>
  <ReplayControl disabled />
        <ForwardControl disabled/>
        <CurrentTimeDisplay  disabled/>
        <TimeDivider disabled/>
        <PlaybackRateMenuButton  disabled />
        <VolumeMenuButton />
      </ControlBar>
      </Player>
      {/* <video playsInline id="video" className="video" muted autoPlay loop>
        <source src="final.webm" type="video/webm" />
        Your browser does not support the video tag.
      </video> */}
    </div>
  );
}

export default App;
